import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

class Intructions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showModal: this.props.showModal
    };
  }

  toggle = () => {
    this.setState({showModal: !this.state.showModal})
  }

  render() {
    return (
      <div>
        <Button className="btn-instructions" color="warning" onClick={this.toggle}>Instrucciones</Button>        
        <Modal
          isOpen={this.state.showModal}
          toggle={this.toggle}
          className={this.props.className}
          size="lg"
        >
          <ModalHeader toggle={this.toggle}> <h4>Intrucciones</h4></ModalHeader>
          <ModalBody>
          <h5>Bienvenido a <b>"La Votadera"</b> de tiempo!!</h5>
            <p>
              A continuacin encontrars las instrucciones, aunque si quieres puedes probar suerte sin leer nada.
            </p> 
            <p>
              La idea es hacer que tu candidato gane, ante ese deseo se va a interponer la <b>Fotocopiadura Nacional</b> del Estado Civil de Colombia 
              haciendo trampas para evitar que tu candidato salga con la mayor cantidad de votos. 
            </p>
            <p>
              Primero debes seleccionar tu candidato, luego en el campo para votos debes poner una cifra desde 1 hasta 100.000 y posteriormente 
              presionar el botn votar. (Puedes cambiarla cuantas veces quieras)
            </p>
            <p>
              En cada votacin tuya <b>la Fotocopiadura</b> le podr asignar a los dems candidatos desde uno hasta el doble de votos de los que le asignaste a tu candidato.
            </p>
            <p>
              La aplicacin cuenta con <b>6 comodines de un solo uso</b>, y estos son unos nmeros especiales, tres de ellos son de 5 dgitos y estn relacionados directamente con las 
              elecciones de la primera vuelta. Los otros tres son de 4 dgitos y representan fechas especiales para <b>La Democracia de Colombia</b>.
              Los comodines funcionan mejor al principio pues te pueden ayudar a tomar la delantera y una vez se use un comodn ya no funcionar de nuevo.
            </p>
            <p>
              La aplicacin tambin cuenta con una funcin que premia tu suerte (si la tienes) pero debes poner mucha atencin a los cambios en 
              cada votacin porque habr una seal en los nmeros que te indicar que es el momento para aprovechar esa ventaja, pero si no la aprovechas, 
              debers esperar hasta otra oportunidad.
            </p>
            <p>
              Pd: los <b>nmeros primos</b> pueden ser tu mejor amigo en ciertos momentos, pero ese momento es el que t debes descubrir.
            </p>
            <p>Cuando termines monta tu Print Screen en redes con el Hashtag <b>#LaVotadera</b> Suerte con tu candidato!! :)  </p>
            <p>
              Esta vaina se ve mejor con la fuente oficial y <a href="http://2ttf.com/download/DVb26GzXk0k" > la descargas desde aqu!!</a> va 2ttf
            </p>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.toggle}>
              De acuerdo
            </Button>{" "}            
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default Intructions;



